.headline {
  grid-area: headline;
  align-self: center;
  color: #fff;
  font-family: Helvetica, sans-serif;
  font-size: 5.25rem;
  line-height: 5.25rem;
  letter-spacing: -2.2px;
}

.subhead {
  grid-area: subhead;
  align-self: center;
  color: #fff;
  font-size: 0.875rem;
  line-height: 1.625rem;
  letter-spacing: normal;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  
}


/*cta css*/

.cta {
  background-color: #fff;
  color: #000;
  grid-area: cta;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.cta > p {
  position: relative;
  


    font-family: inherit;
    font-size: 1.5rem;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 2rem;
    letter-spacing: -0.63px;
  
  
  
}

.navlinks__link--talk {
  text-decoration: none;
  color: #000;
  font-family: Helvetica, sans-serif;
  font-size: 0.87rem;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.82;
  letter-spacing: 1px;
  text-transform: uppercase;
}

@media only screen and (max-width: 720px) {
  .marquee {
 
    grid-template-areas: "headline"
                         "subhead"
                         "cta"
                         "cta";

  }
  
  .cta {
    flex-direction: column;
    justify-content: center;
    
  }

}

